import * as Yup from "yup";

const REQUIRED_FIELD = Yup.string().required("  ");

export const SingInSchema = Yup.object().shape({
  email: Yup.string()
    .required("  ")
    .email("  email"),
  password: Yup.string()
    .required("  ")
    .min(6, "  ")
    .matches(/(?=.*[0-9])/, "   ")
    .matches(/(?=.*[A-Z])/, "    "),
});

export const SingUpSchema = Yup.object().shape({
  firstName: REQUIRED_FIELD,
  lastName: REQUIRED_FIELD,
  email: Yup.string().required("  ").email(),
  password: Yup.string()
    .required("  ")
    .min(6, "  ")
    .matches(/(?=.*[0-9])/, "   ")
    .matches(/(?=.*[A-Z])/, "    "),
  doublePassword: Yup.string()
    .required("  ")
    .oneOf([Yup.ref("password"), null], "  "),
});

export const CreateDeliverySchema = Yup.object().shape({
  title: REQUIRED_FIELD,
  deliveryMethod: REQUIRED_FIELD,
  country: Yup.string()
    .required("  ")
    .min(4, "  "),
  city: Yup.string()
    .required("  ")
    .min(4, "  "),
  street: Yup.string()
    .required("  ")
    .min(4, "  "),
  house: Yup.number()
    .required("  ")
    .min(1, "      1"),
  index: Yup.string()
    .required("  ")
    .matches(/^[0-9]+$/, "  ")
    .min(6, "  ")
    .max(6, "  "),
  fromCountry: Yup.string()
    .required("  ")
    .min(4, "  "),
  fromCity: Yup.string()
    .required("  ")
    .min(4, "  "),
  fromStreet: Yup.string()
    .required("  ")
    .min(4, "  "),
  fromHouse: Yup.number()
    .required("  ")
    .min(1, "      1"),
  fromIndex: Yup.string()
    .required("  ")
    .matches(/^[0-9]+$/, "  ")
    .min(6, "  ")
    .max(6, "  "),
  rate: REQUIRED_FIELD,
});
